import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { Memory } from '../../../base/memory'
import { Notify } from '../../../base/notify/notify'
import { NotifyConfig } from '../../../base/notify/notify-config'
import { AuthService } from '../../../services/auth.service'

@Component({
  selector: 'ngx-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  mobile: string;
  password: string;

  constructor(private authService: AuthService,
              private router: Router) {
  }

  ngOnInit() {

  }

  login() {
    this.authService.login({ username: this.mobile, password: this.password }).subscribe((res: any) => {
      if (res && res.access_token) {
        const notifyConfig = new NotifyConfig(
          Notify.Type.SUCCESS,
          Notify.Placement.TOP_LEFT,
          Notify.TEMPLATES.Template2,
          'login successful!'
          ,
          ''
        );
        Notify.showNotify(notifyConfig);
        Memory.setToken(res.access_token);
        this.router.navigateByUrl('/pages/iot-dashboard')
      }
    },error => {
      const notifyConfig = new NotifyConfig(
        Notify.Type.DANGER,
        Notify.Placement.TOP_LEFT,
        Notify.TEMPLATES.Template2,
        'incorrect username or password!'
        ,
        ''
      );
      Notify.showNotify(notifyConfig);
    })
  }
}
